/* a simple, rounded-corner "card" - hoverable */

.SimpleCard {
    border: 1px rgba(200, 200, 200, 0.5) solid;
    background-color: rgba(200, 200, 200, 0.3);
    border-radius: 4px;
    /* box-shadow: 4px 4px 4px rgba(200, 200, 200, 0.5); */
    /* padding: 4px; */
    margin: 0 4px 4px 4px;
    padding: 4px;
}

.SimpleCard+.SimpleCard {
    margin-top: 6px;
}

.SimpleCard:hover {
    border: 1px rgba(200, 200, 200, 0.8) solid;
    background-color: rgba(200, 200, 200, 0.4);
    margin-right: 4px;
    box-shadow: 4px 4px 4px rgba(200, 200, 200, 0.8);
}

.SimpleCard .SimpleCard-hidden {
    display: none;
}

.SimpleCard:hover .SimpleCard-hidden {
    display: unset;
}

.kbCard {
    border: 1px rgba(200, 200, 200, 0.5) solid;
    background-color: rgba(200, 200, 200, 0.3);
    border-radius: 4px;
    /* box-shadow: 4px 4px 4px rgba(200, 200, 200, 0.5); */
    /* padding: 4px; */
    margin: 10px 0 10px 0;
}

.kbCard-header {
    background-color: rgba(200, 200, 200, 0.5);
    padding: 4px;
}

.kbCard-body {
    padding: 4px;
}

.kbCard:hover {
    border: 1px rgba(200, 200, 200, 0.8) solid;
    background-color: rgba(200, 200, 200, 0.5)
    /* box-shadow: 4px 4px 4px rgba(200, 200, 200, 0.8); */
}

